import React, { Component } from 'react';

class About extends Component {
  render() {
    return (
      <div className="content">
          <h2>About</h2>

          <p className="about">Hello, my name is Candace, and I created Puppedia. Puppedia is an encyclopedia of dog breeds, and through browsing you can learn all about your favorite breeds.</p>
          
      </div>
    );
  }
}

export default About;



